<template>
  <div v-if="cinemalist">
        <oHeader @back="back">{{cinemalist.name}}</oHeader>
        <!-- Services satrt -->
        <div class="ServicesNav"  @click="mask=true" v-if="cinemalist.services">
            <ul>
                <li v-for="(data,index) in getServices" :key="index">{{data.name}}</li>
            </ul>
        </div>
        <!-- Services end -->

        <!-- 地址 起始 -->
        <div class="oUrl">
            <span class="iconfont icondiqiu"></span>
            <p>{{cinemalist.address}}</p>
            <span class="iconfont icondianhua"></span>
        </div>
        <!-- 地址 结尾 -->

        <!-- logo start -->
        <div class="cinemaLogo">
            <Loop cName="cinemaDetils-loop" class="loop">
                <div v-for="(data,index) in filmslist" :key="data.filmId" class="swiper-slide loop-box" v-swiper="{index:index,datalist:filmslist,num:2,space:10,cName:'cinemaDetils-loop',slide:true}">
                    <img :src="data.poster" alt="">
                </div>
            </Loop>
        </div>
        <!-- logo end -->

        <!-- text start -->
        <div class="text">
            <div v-for="data in filmslist" :key="data.filmId">
                <p>{{data.name}} <i style="color:orange">{{data.grade}}分</i></p>
                <span>{{data.category}} | {{data.runtime}}分钟 | {{data.actors | filterName}}</span>
            </div>
        </div>
        <!-- text end -->

        <!-- mask start -->
        <div class="mask" v-if="mask">
            <oHeader @back="mask=false">{{cinemalist.name}}</oHeader>
            <ul>
                <li v-for="(data,index) in cinemalist.services" :key="index">
                    <p>{{data.name}}</p>
                    <span>{{data.description}}</span>
                </li>
            </ul>
        </div>
        <!-- mask end -->
  </div>
</template>

<script>
import Loop from '@/components/Loop'
import oHeader from '@/components/oHeader'
import {instance} from '@/utils/http'
import {mapMutations,mapState} from 'vuex'
export default {
    components:{
        oHeader,Loop
    },
    computed:{
        ...mapState("cinema",["filmsId"]),
        getServices(){
            if(!this.cinemalist.services){
                return this.cinemalist.services.map(item=>item)
            }
            
        }
    },
    methods:{
        ...mapMutations("Tabber",["hide","show"]),
        ...mapMutations("cinema",["changeFilmsId"]),
        back(){
            this.$router.back()
        }
    },
    data(){
        return{
            filmslist:null,
            cinemalist:null,
            mask:false
        }
    },
    created(){
        this.hide()
        instance(`/gateway/?cinemaId=${this.$route.params.id}&k=6871116`,{
            headers:{
                'X-Host':'mall.film-ticket.film.cinema-show-film'
            }
        }).then(res=>{
            console.log(res.data.data.films)
            this.filmslist = res.data.data.films
        })
        instance(`/gateway/?cinemaId=${this.$route.params.id}&k=1825929`,{
            headers:{
                'X-Host':'mall.film-ticket.cinema.info'
            }
        }).then(res=>{
            this.cinemalist = res.data.data.cinema
            console.log(this.cinemalist)
        })
    },
    beforeDestroy(){
        this.show()
    }
}

</script>
<style lang="scss" scoped>
.mask{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 40px;
    left: 0;
    background: white;
    z-index: 10;
    padding: 0 15px;
    ul{
        width: 100%;
        li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
        p{
            border: 1px solid orange;
            color: orange;
        }
        span{
            width: 200px;
        }
        }
    }
}
.text{
    width: 100%;
    text-align: center;
    p{
        line-height: 30px;
        font-size: 16px;
    }
    span{
        margin: 0 auto;
        display: block;
        width: 270px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
.cinemaLogo{
    width: 100%;
    height: 160px;
    background: skyblue;
    display: flex;
    align-items: center;
    .loop{
        width: 100%;
        .loop-box{
            width: 100%;
            height: 100%;
        img{
            width: 90px;
            // height: 130px;
        }
        }
    }
}
.oUrl{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    margin-top: 15px;
    margin-bottom: 15px;
}
.ServicesNav{
    width: 100%;
    margin-top: .4rem;
    ul{
        display: flex;
        justify-content: center;
        align-items: center;
        li{
            margin: 0 2px;
            border: 1px solid orange;
            color: orange;
            padding: 2px;
            font-size: 12px;
        }
    }
}
</style>